import React from 'react';
import banner from './assets/banner.png';
import money from './assets/moneystack.jpg';
import Celebrate from './components/Celebrate';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            goal: 0,
            current: 0,
            percentcomplete: 0,
            history: [],
            celebration: false,
            addition: false,
            subtraction: false,
            goaltoggle: true,
            historytoggle: false,
            date: '',
            amount: 0
        };
        this.percentChange = this.percentChange.bind(this)
    }

    //div growing math

    percentChange = () => {
        let goal = parseFloat(this.state.goal);
        let current = parseFloat(this.state.current);
        let newpercent = ((current / goal) * 100);
        this.setState({ percentcomplete: newpercent });
    }

    ///every amount update

    captureData = (e) => {
        //take date+amount and push into this.state.history
        if (this.state.date !== '' && this.state.amount !== 0) {
            this.setState({ history: [...this.state.history, [this.state.date + " ", this.state.amount]] });

            //set new current amount
            let hold = 0
            hold += parseFloat(this.state.current) + parseFloat(this.state.amount);
            this.setState({ current: (hold) });

            //reset state
            this.setState({ date: '' });
            this.setState({ amount: 0 });

            e.target.value = ''
            //no goal set yet!
            if (this.state.goal <= 0) {
                alert('no goal set!');
            }

            return
        } else { return alert("set date and amount") }
    }

    //toggles

    toggleAdditionOn = () => {
        this.setState({ addition: true })
    }
    toggleAdditionOff = () => {
        this.setState({ addition: false })
    }
    toggleSubtractionOn = () => {
        this.setState({ subtraction: true })
    }
    toggleSubtractionOff = () => {
        this.setState({ subtraction: false })
    }
    toggleGoalOff = () => {
        this.setState({ goaltoggle: false })
    }
    toggleGoalOn = () => {
        this.setState({ goaltoggle: true })
    }

    toggleHistoryOn = () => {
        this.setState({ historytoggle: true })
    }

    toggleHistoryOff = () => {
        this.setState({ historytoggle: false })
    }

    startGoalDisplay = () => {
        this.setState({ goaltoggle: false });

    }

    goalMet = () => {
        if (this.state.goal !== 0 && this.state.goal <= this.state.current) {
            this.setState({ celebration: true })
            return null
        }
        if (this.state.goal > this.state.current) {
            this.setState({ celebration: false })
        }
    }

    //update states with use input

    updateMyDate = (e) => {
        this.setState({ date: (e.target.value) });
    }
    updateMyAmount = (i, e) => {
        this.setState({ amount: (i * e.target.value) });
    }
    updateMyGoal = (e) => {
        this.setState({ goal: (e.target.value) });
    }

    //addition tab

    appearAddition = () => {
        if (this.state.addition === true) {
            return (
                <div className="additionpopup">
                    <h2>Update Your Cash</h2>
                    <form id="additionform">
                        <p>Date</p>
                        <input onChange={(e) => { this.updateMyDate(e) }} placeholder="Aug. 13"></input>
                        <p>$ Amount to Add</p>
                        <input onChange={(e) => { this.updateMyAmount(1, e) }} placeholder="0.00"></input>
                        <br></br>
                        <br></br>
                        <button type="button" onClick={(e) => {
                            this.captureData(e);
                            document.forms["additionform"].reset();
                        }}>+ Money Stack</button>
                    </form>
                    <br></br>
                    <button id="exitX" onClick={() => {
                        this.toggleAdditionOff();
                        this.percentChange();
                        document.forms["additionform"].reset();
                        this.goalMet();
                    }
                    }>Done</button>
                </div>
            );
        } else { return (null); }
    }

    //subtraction tab

    appearSubtraction = () => {
        if (this.state.subtraction === true) {
            return (
                <div className="additionpopup">
                    <h2>Update Your Cash</h2>
                    <form id="additionform">
                        <p>Date</p>
                        <input onChange={(e) => { this.updateMyDate(e) }} placeholder="Aug. 13"></input>
                        <p>$ Amount to Subtract</p>
                        <input onChange={(e) => { this.updateMyAmount(-1, e) }} placeholder="0.00"></input>
                        <br></br>
                        <br></br>
                        <button type="button" onClick={(e) => {
                            this.captureData(e);
                            document.forms["additionform"].reset();
                        }}>+ Money Stack</button>
                    </form>
                    <br></br>
                    <button id="exitX" onClick={() => {
                        this.toggleSubtractionOff();
                        this.percentChange();
                        document.forms["additionform"].reset();
                        this.goalMet();
                    }
                    }>Done</button>
                </div>
            );
        } else { return (null); }
    }


    //history tab

    appearHistory = () => {
        if (this.state.historytoggle === true) {
            return (
                <div className="additionpopup">
                    <h2>History</h2>
                    {this.state.history.map((item, index) => (
                        <p key={index}>{item}</p>
                    ))}
                    <button id="exitY" onClick={() => {
                        this.toggleHistoryOff();
                    }
                    }>Done</button>
                </div>
            );
        } else { return (null); }
    }

    //goal start tab

    appearGoal = () => {
        if (this.state.goaltoggle === true) {
            return (
                <div id="setgoal">
                    <p>{'$ '}   <input id="goalamountbox" placeholder="Goal Amount" onChange={(e) => {
                        this.updateMyGoal(e)
                    }}>
                    </input>
                    </p>
                    <button id='set'
                        onClick={() => {
                            this.startGoalDisplay();
                            this.percentChange();
                        }}>set</button>
                </div>
            )
        } else { return (<p id="thegoalnumber">${this.state.goal}</p>) }
    }

    render() {
        return (
            <div>
                <div id="main">

                    <div id="title">
                        <img id="goalBanner" src={banner} alt="Goal banner" />
                    </div>

                    <div id="goalnumber">{this.appearGoal()}</div>

                    <div id="percentEmpty">

                        <div id="percentComplete"
                            style={{ height: (this.state.percentcomplete + "%") }}
                        >
                            <img id="cashstack" src={money} alt="cash stack"
                            ></img>
                        </div>
                    </div>

                    <div id="white">
                        <p id="currentstate">Current Amount: ${this.state.current}</p>

                        <div id='history'>
                            <button type="button" onClick={() => {
                                this.toggleHistoryOn();
                            }}>History</button>
                        </div>
                    </div>

                    <div id="buttons">
                        <button id="add" onClick={() => {
                            this.toggleAdditionOn();
                        }}> + </button>

                        <button id="sub" onClick={() => {
                            this.toggleSubtractionOn();
                        }}> - </button>
                    </div>
                    {this.appearAddition()}
                    {this.appearHistory()}
                    {this.appearSubtraction()}

                </div >
                {/* {this.appearCelebration()} */}
                <Celebrate celeb={this.state.celebration} />

            </div>
        );
    }
}


export default Home